}

.search__input {
  color: #fff;
  width: 394px;
  background: none;
  border: rgba(0, 0, 0, 0);
  border-bottom: 1px solid #fff;
  outline: none;
  font-size: 14px;
  line-height: 1.14;
}

.search__btn {
  background-color: initial;
  color: #fff;
  border: rgba(0, 0, 0, 0);
  padding: 0;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
