  right: 0;
}

.storage {
  justify-content: center;
  padding-left: 0;
  display: flex;
}

.storage__item:first-child {
  margin-right: 15px;
}

.storage__input {
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
