  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 1.33333;
}

.library__btn.active {
  background: #ff6b01;
  border: 1px solid #ff6b01;
}

.storage__btn {
  min-width: 130px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 5px;
  padding-top: 14px;
  padding-bottom: 12px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: block;
}

.storage__input:checked ~ .storage__btn {
