}

.nav--item.login {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  position: relative;
}

.error-message {
  color: red;
}

.current:after {
  content: "";
  height: 3px;
  width: 100%;
  background-color: #ff6b08;
  position: absolute;
  bottom: -3px;
  left: 0;
}

.modal-login {
