}

.modal-login__login, .modal-login__welcome {
  padding: 10px 0;
}

.login-form {
  width: 100%;
  flex-flow: column wrap;
  justify-content: center;
  display: flex;
}

.login-form > input {
  height: 50px;
  height: 50px;
  -webkit-box-shadow: none;
  box-shadow: none;
