}

.login-form__alternate-access {
  color: #b2becc;
  margin: 0 auto;
  padding-top: 20px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

