:root {
  --main-background-color: #212121;
  --background-overlay: rgba(11, 11, 11, .5);
  --main-title-color: # #ff6b01;
  --footer-text-color: #545454;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --main-btn-color: silver;
  --inv-btn-main-color: #3f3f3f;
  --animation-duration: 250ms;
  --transition-bg-color: background-color 250ms cubic-bezier(.4, 0, .2, 1);
  --accent-color: rgba(1, 60, 255, 0);
  --accent-link: #c95d0f;
  --icons-color: #afb1b8;
  --accent-link-footer: rgba(255, 255, 255, .1);
  --transition-fill: fill 250ms cubic-bezier(.4, 0, .2, 1);
  --section-filter-link-tablet-set: 2;
  --section-filter-link-tablet-gap: 30px;
  --section-filter-link-desktop-set: 3;
  --section-filter-link-desktop-gap: 100px;
}

.social-network {
  display: inline-flex;
}

.social-network__item:not(:first-child) {
  margin-left: 10px;
}

.social-network__link {
  width: 44px;
  height: 44px;
  transition: var(--transition-bg-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social-network__link:hover, .social-network__link:focus {
  background-color: var(--accent-link);
}

.social-network__link--change {
  background: var(--accent-link-footer);
  transition: var(--transition-bg-color);
}

.social-network__icon {
  fill: var(--icons-color);
  transition: var(--transition-fill);
}

.social-network__link:hover .social-network__icon, .social-network__link:focus .social-network__icon, .social-network__icon--change {
  fill: var(--main-btn-color);
}

.team-modal {
  z-index: 100;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--animation-duration) var(--timing-function);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.team-modal.is-open {
  opacity: 1;
  pointer-events: initial;
}

.team-modal-overlay {
  width: 100vw;
  height: 100vh;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  transition: opacity var(--animation-duration) var(--timing-function);
  background-color: rgba(0, 0, 0, .8);
  position: absolute;
  top: 0;
  left: 0;
}

.team-modal-content {
  width: 74vw;
  height: 300vw;
  background-color: var(--main-background-color);
  transition: transform 1000ms var(--timing-function);
  border-radius: 10px;
  margin: 0;
  padding: 0;
  display: flex;
  position: relative;
  transform: scale(1);
  box-shadow: 2px 4px 12px #ff6b01, 0 2px 5px rgba(255, 255, 255, .506), 0 2px 3px -1px rgba(255, 255, 255, .137);
}

@media screen and (min-width: 768px) {
  .team-modal-content {
    height: 95vw;
  }
}

@media screen and (min-width: 1200px) {
  .team-modal-content {
    height: 49vw;
  }
}

.team-modal-content-box {
  position: absolute;
  top: 80px;
  left: 40px;
  right: 40px;
}

@media screen and (min-width: 768px) {
  .team-modal-content-box {
    margin-left: calc(-1 * var(--section-filter-link-tablet-gap) );
    flex-wrap: wrap;
    margin-bottom: -30px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .team-modal-content-box {
    margin-left: calc(-1 * var(--section-filter-link-desktop-gap) );
  }
}

.team-modal.is-open .team-img-card {
  transform: scale(.6);
}

.team-img-card {
  height: 45vw;
  transition: transform 1000ms var(--timing-function);
  background-color: var(--accent-color);
  border-radius: 10px 10px 5px 5px;
  transform: scale(2);
  box-shadow: 2px 4px 12px #cfe414, 0 2px 5px rgba(255, 255, 255, .506), 0 2px 3px -1px rgba(255, 255, 255, .137);
}

@media screen and (min-width: 768px) {
  .team-img-card {
    height: 20vw;
  }
}

@media screen and (max-width: 767px) {
  .team-img-card:not(:last-child) {
    margin-bottom: 30px;
  }
}

@media screen and (min-width: 768px) {
  .team-img-card {
    flex-basis: calc(100% / var(--section-filter-link-tablet-set)  - var(--section-filter-link-tablet-gap) );
    margin-left: var(--section-filter-link-tablet-gap);
    margin-bottom: 50px;
  }
}

@media screen and (min-width: 1200px) {
  .team-img-card {
    flex-basis: calc(100% / var(--section-filter-link-desktop-set)  - var(--section-filter-link-desktop-gap) );
    margin-left: var(--section-filter-link-desktop-gap);
  }
}

