.team-close-btn {
  width: 30px;
  height: 30px;
  background: var(--accent-color);
  cursor: pointer;
  transition: opacity var(--animation-duration);
  border: 2px solid rgba(192, 192, 192, .506);
  border-radius: 50%;
  position: absolute;
  top: 22px;
  right: 28px;
}

.team-close-btn:hover, .team-close-btn:focus {
  background-color: var(--accent-link);
  transform: scale(1.1);
}

.material-icons.team-closer {
  fill: var(--icons-color);
  transition: opacity var(--animation-duration) var(--timing-function);
  position: absolute;
  top: 14px;
  transform: translate(-50%, -50%);
}

.material-icons.team-closer:hover .material-icons.team-closer, :focus .material-icons.team-closer {
  opacity: .8;
  fill: var(--accent-link);
}

.slide-team.card-1 {
  animation: slide-team 2s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-2 {
  animation: slide-team .2s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-3 {
  animation: slide-team 1.2s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-4 {
  animation: slide-team 1.1s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-5 {
  animation: slide-team 1.2s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-6 {
  animation: slide-team .4s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-7, .slide-team.card-8 {
  animation: slide-team 1.6s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-9 {
  animation: slide-team 2.4s cubic-bezier(.23, 1, .32, 1) both;
}

.slide-team.card-10, .slide-team.card-11, .slide-team.card-12 {
  animation: slide-team 2s cubic-bezier(.23, 1, .32, 1) both;
}

@keyframes slide-team {
  0% {
    transform-origin: 0 100%;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0;
    transform: translate(1000px, 1000px)skew(80deg, 10deg);
  }

  100% {
    transform-origin: 50%;
    -webkit-filter: blur();
    filter: blur();
    opacity: 1;
    transform: translate(0)skew(0deg);
  }
}

